
import Vue from "vue"

export default Vue.extend({
  props: {
    name: {
      type: String,
      required: true,
    },
    artist: {
      type: String,
      required: false,
      default: null,
    },
    url: {
      type: String,
      required: true,
    },
    image: {
      type: String,
      required: true,
    },
    nowPlaying: {
      type: Boolean,
      required: false,
      default: false,
    },
    plays: {
      type: Number,
      required: false,
      default: null,
    },
  },
})
